#mobile-menu {
    display: none;
}

.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    border-radius: 1em;
    background-color: black;
    margin: 6px 0;
    transition: 0.4s;
}

/* Rotate first bar */
.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
    transform: rotate(-45deg) translate(-9px, 6px) ;
}

/* Fade out the second bar */
.change .bar2 {
    opacity: 0;
}

/* Rotate last bar */
.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
    transform: rotate(45deg) translate(-8px, -8px) ;
}

/* Rotate first bar */
.changeBack .bar1 {
    -webkit-transform: rotate(45deg) translate(9px, -6px) ;
    transform: rotate(45deg) translate(9px, -6px) ;
}

/* Fade out the second bar */
.changeBack .bar2 {
    opacity: 1;
}

/* Rotate last bar */
.changeBack .bar3 {
    -webkit-transform: rotate(-45deg) translate(8px, 8px) ;
    transform: rotate(-45deg) translate(8px, 8px) ;
}

.navbar-custom {
    font-weight: 700;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    box-shadow: 5px 5px 8px #888888;
}

.navbar-light .navbar-brand {
    color: #000000;
}

.navbar-light .navbar-brand:hover {
    color: #fcb33b;
}

.nav-link {
    color: black;
}

.nav-link:hover {
    color: #fcb33b;
}

.ul-custom {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.li-custom {
    float: left;
}
