#contact {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3em;
    margin-bottom: 1em;
    background-color: #ffffff;
    border: 1px solid black;
    border-radius: 3em;
}

#contact-title {
    margin-top: 1em;
}

#contact-types {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 2em;
    margin-bottom: 2em;
}

.contact-custom {
    margin-top: 1em;
    margin-bottom: 1em;
}
