.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    border-radius: 1em;
    background-color: black;
    margin: 6px 0;
    transition: 0.4s;
}

/* Rotate first bar */
.change .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px) ;
}

/* Fade out the second bar */
.change .bar2 {
    opacity: 0;
}

/* Rotate last bar */
.change .bar3 {
    transform: rotate(45deg) translate(-8px, -8px) ;
}

/* Rotate first bar */
.changeBack .bar1 {
    transform: rotate(45deg) translate(9px, -6px) ;
}

/* Fade out the second bar */
.changeBack .bar2 {
    opacity: 1;
}

/* Rotate last bar */
.changeBack .bar3 {
    transform: rotate(-45deg) translate(8px, 8px) ;
}

