#home {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1em;
    margin-bottom: 1em;
    color: white;
}

#mission {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#header-HP {
    margin-top: 2em;
    margin-bottom: 2em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#newsletter-HP {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#newsletter {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
}

#newsletter-email {
    border: 2px solid black;
    border-radius: 1em;
    background-color: white;
    height: 1.5em;
    margin-bottom: 5px;
    font-size: 20px;
}

#newsletter-submit {
    color: white;
    background-color: rgb(211, 58, 211);
    border: 2px solid black;
    border-radius: 1em;
    font-weight: 700;
    font-size: 15px;
    width: 50%;
}

